a {
  text-decoration: solid;
  color: #338e50;
}

.hero-section {
  display: flex;
  flex-direction: column;
  align-self: center;
  height: 100%;
  max-width: 100%;
}

.hero-image > img {
  width: 100%;
  padding: 0 5%;
}

.hero-3-boxes {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 20px;
  padding: 0 8%;
  margin-top: -10%;
  z-index: 1;
}

.hero-box-left,
.hero-box-cent,
.hero-box-right {
  display: flex;
  flex-direction: column;
  height: 250px;
  width: 33%;
}

.hero-box-left {
  justify-content: center;
  background-color: #3b9658;
  color: white;
}

.hero-box-left > b {
  display: block;
  margin-bottom: 10px;
  text-align: center;
  padding: 10px 20px 0 20px;
}

.hero-box-left > p {
  padding: 0 20px 10px 20px;
}

.hero-box-cent > img {
  height: 300px;
}

.hero-box-right > img {
  height: 300px;
}

.hero-box {
  width: 25%;
}

.hero-box > b {
  display: block;
  margin-bottom: 1rem;
}

.hero-4-boxes {
  display: flex;
  flex-direction: row;
  padding: 50px 8%;
  gap: 3%;
}

@media screen and (max-width: 1150px) {
  .hero-box-left {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 1000px) {
  .hero-3-boxes {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .hero-box-left {
    display: flex;
    flex-direction: column;
    height: fit-content;
    padding: 20px 0;
    width: 400px;
  }
  .hero-box-cent,
  .hero-box-right {
    height: 300px;
    width: 400px;
    padding-bottom: 20px;
  }

  .hero-4-boxes {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 50px 8%;
    gap: 8%;
  }

  .hero-box {
    width: 40%;
  }
}

@media screen and (max-width: 800px) {
  .hero-3-boxes {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1rem;
    padding: 0;
  }

  .hero-box-left {
    display: flex;
    flex-direction: column;
    height: fit-content;
    width: 100%;
  }
  .hero-box-cent,
  .hero-box-right {
    width: 100%;
    padding-bottom: 0;
  }

  .hero-4-boxes {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 0;
    gap: 30px;
  }

  .hero-box {
    padding-bottom: 20px;
  }

  .hero-image img {
    padding: 0;
  }
}
