.navbar {
  background-color: #f1f1f1;
  display: flex;
  flex-direction: column;
}

.navbar-top {
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  gap: 30px;
  margin: 0 10%;
  padding-top: 20px;
}

.navbar-right-links > a {
  text-decoration: none;
  text-transform: uppercase;
}

.navbar-right-links img {
  vertical-align: middle;
}

.navbar-right-links {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  align-self: center;
}

.navbar-bottom {
  background-color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-left: 10%;
  margin-right: 10%;
  padding-top: 20px;
  text-transform: uppercase;
}

.navbar-bottom > a {
  text-decoration: none;
  color: black;
}

.navbar-logo {
  padding-left: 70px;
}

@media screen and (max-width: 1150px) {
  .navbar-top {
    flex-direction: column;
  }

  .navbar-logo,
  .navbar-right-links {
    justify-content: center;
    align-self: center;
    padding: 0 0;
  }

  .navbar-right-links a {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .navbar-right-links img {
    display: none;
  }
}
